.content {
	width: 520px;
	height: 910px;
	background-color: #f3f3f5;
  top: 0px;
  left: 0px; 
	overflow: scroll;
	position: fixed;
	text-align: justify;

  padding-left: 30px;
  padding-right: 30px;
	-moz-box-sizing: border-box;
	box-sizing: border-box;

	z-index: 0;
}

.pointsBalance {
  color: gray;
  background-color: white;

  display: table-cell;
  width: 520px;
  height: 110px;
  left: 0px;
  top: 0px;
  padding: 10px;

  border-style: solid;
  border-width: 1px;
  border-color: black;

  vertical-align: bottom;
  text-align: center;

  font-family: 'Nunito', sans-serif;
  font-size: 12pt;
}

.textContent {
  padding: 30px;
}

.footer {
	position: fixed;
	width: 510px;
	top: 910px;
	height: 100px;
	background-color: white;
	border-top-width: 1px;
	border-top-style: solid;
	border-top-color: gray;
	font-size: 0px;
	z-index: 1;
	font-size: 0pt;
}

.footerMenu {
	text-align: justify;
	padding-left: 30px;
	padding-right: 30px;
}

.footerMenu li {
	display: inline-block;
	width: 25%;
	text-align: center;
	font-family: 'Nunito', sans-serif;
	font-size: 16pt;
}
.footerMenu:after {
	width: 100%; /* Ensures justification for single lines */
	display: inline-block;
}

.menuButton {
	text-align: center;
	display: inline-block;
	font-size: 15px;
	padding: 15px;
	color: gray;
	cursor: pointer;
}

.menuButtonSelected {
	color: black;
}

.menuButtonIcon {
	font-size: 30px;
	padding: 5px;
}

.app {
	width: 520px;
	height: 1030px;
}

.iPhone {
	position: fixed;
	left: 0px;
	top: 0px;
	z-index: 999;
	pointer-events: none;
	background: url('iPhone.png');
	width: 520px;
	height: 1030px;
}

.header {
	position: fixed;
	width: 520px;
	top: 30px;
	height: 35px;
	background-image: linear-gradient(to bottom, white, rgba(255, 0, 0, 0));
	z-index: 1;
	font-family: 'Helvetica Neue', Helvetica, sans-serif;
	font-size: 11pt;
	padding-top: 10px;
}

.headerTime {
	left: 55px;
	width: 70px;
	text-align: right;
	z-index: 2;
	position: fixed;
}

.headerStatus {
	left: 390px;
	width: 75px;
	z-index: 2;
	position: fixed;
}

.subContent {
	border-radius: 15px;
	background: #7aa65d;
	padding: 10px;
	width: 100%;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	color: white;

	float: left;
}

.subContent button {
	border-radius: 15px;
	width: 175px;
	height: 50px;
	color: #7aa65d;
}

.tram {
	width: 55%;
	float: right;
	shape-image-threshold: 0.5;
	shape-outside: url(trammask.png);
}

.content p,
.content body,
.content button {
	margin: 0;
	font-family: 'Nunito', sans-serif;
	font-size: 16pt;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.content button {
	margin-top: 0.5em;
	cursor: pointer;
}

.content h1,
.content h2,
.content h3,
.content h4,
.content h5 {
	font-family: 'Dancing Script', cursive;
}

.content h1,
.content h2,
.content h3,
.content h4,
.content h5,
.content button,
.content p {
	padding-bottom: 0px;
	margin-bottom: 0em;
	margin-top: 0.6em;
}
